.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}


.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.card-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 0 4%;
}

.card {
  width: 15%;
  min-width: 200px;
  height: auto;
  margin: 30px 10px;
  cursor: pointer;
  background-color: #fff;
  box-shadow: 0 2px 10px rgb(0 0 0 / 30%);
}

.card a {
  text-decoration: none;
  color: #fff;
}

.card:hover{
  box-shadow: 3px 3px 20px rgb(0 0 0 / 30%);
}

.card .img img {
  width: 100%;
}

.card .details {
  padding-left: 10px;
  margin: 10px auto;
  height:100px;
}

.card .details h3 {
  text-align: left;
  font-size: 16px;
  font-weight: 400;
  letter-spacing: 0.5px;
  color: rgb(27, 25, 25);
}

.card .details h4 {
  margin-top: 10px;
  text-align: left;
  font-size: 12px;
  color: rgb(110, 110, 110);
}

.card .details h5 {
  margin-top: 10px;
  text-align: left;
  font-size: 15px;
  letter-spacing: 2px;
  color: #009688;
}

.card .description{
  position: relative;
  height: 100px;
  overflow: hidden;
  text-align: start;
  padding: 5px 10px;
}

.cart-btn{
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  margin: 5px;
}

.movies {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  flex-direction: row;
}

.App-header {
  background-color: #282c34;
  height: 70px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  padding: 20px;
  cursor: pointer;
}

.search-bar input{
  margin-top: 20px;
  margin-right: 10px;
  width: 30%;
  height: 30px;
}

.search-btn{
  height: 35px;
}